@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Roboto Slab', serif;box-sizing:border-box}* ::-webkit-scrollbar{width:0.5em}* ::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:lightgrey}* ::-webkit-scrollbar-thumb{background-color:grey;outline:1px solid #A5D6A7}.App{width:100vw;height:100vh;display:flex;flex-direction:column;align-items:center;position:relative;background-color:#FFF;overflow-y:hidden}.App .salad-bowl{background-size:contain;background-position:center;background-repeat:no-repeat;position:absolute;width:34em;height:30em;opacity:0.7;pointer-events:none;bottom:-11em;z-index:0}.App .salad-bowl.left{left:-11em;-webkit-transform:rotate(35deg);transform:rotate(35deg);-webkit-animation:slide-tr 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both;animation:slide-tr 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both}.App .salad-bowl.right{right:-11em;-webkit-transform:rotate(-35deg);transform:rotate(-35deg);-webkit-animation:slide-tl 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both;animation:slide-tl 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both}@-webkit-keyframes slide-tr{0%{-webkit-transform:translateY(30em) translateX(-30em) rotate(35deg);transform:translateY(30em) translateX(-30em) rotate(35deg)}100%{-webkit-transform:translateY(0) translateX(0) rotate(35deg);transform:translateY(0) translateX(0) rotate(35deg)}}@keyframes slide-tr{0%{-webkit-transform:translateY(30em) translateX(-30em) rotate(35deg);transform:translateY(30em) translateX(-30em) rotate(35deg)}100%{-webkit-transform:translateY(0) translateX(0) rotate(35deg);transform:translateY(0) translateX(0) rotate(35deg)}}@-webkit-keyframes slide-tl{0%{-webkit-transform:translateY(30em) translateX(30em) rotate(-35deg);transform:translateY(30em) translateX(30em) rotate(-35deg)}100%{-webkit-transform:translateY(0) translateX(0) rotate(-35deg);transform:translateY(0) translateX(0) rotate(-35deg)}}@keyframes slide-tl{0%{-webkit-transform:translateY(30em) translateX(30em) rotate(-35deg);transform:translateY(30em) translateX(30em) rotate(-35deg)}100%{-webkit-transform:translateY(0) translateX(0) rotate(-35deg);transform:translateY(0) translateX(0) rotate(-35deg)}}

.main-container-comp{width:100%;display:flex;padding-top:4rem;justify-content:center}

.section-container-comp{height:85vh;position:relative;padding-top:4.5rem;background-color:#FFF;z-index:1;-webkit-animation:fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) 0.5s both;animation:fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) 0.5s both}.section-container-comp .section-header{min-width:120px;text-align:center;font-weight:bolder;font-size:1.2em;position:absolute;background-color:#A5D6A7;border:2px solid #388E3C;-webkit-transform:translateX(-50%);transform:translateX(-50%);top:-2.4rem;left:50%;color:#388E3C}.section-container-comp .section-header>div{font-family:'Roboto', sans-serif}.section-container-comp .section-header .app-title{padding:0.3rem 2rem;font-size:1.2em;background-color:#388E3C;color:white}.section-container-comp .section-header .section-title{padding:0.2rem 0}.section-container-comp.has-border{border-radius:0px 0px 0px 0px;-moz-border-radius:0px 0px 0px 0px;-webkit-border-radius:0px 0px 0px 0px;border:2px solid #388E3C}.section-container-comp.right-shadow{box-shadow:7px 7px 0px 0px #388E3C}.section-container-comp.left-shadow{box-shadow:-7px 7px 0px 0px #388E3C}@-webkit-keyframes fade-in{0%{opacity:0}100%{opacity:1}}@keyframes fade-in{0%{opacity:0}100%{opacity:1}}

.fav-comp{height:100%;min-width:30em;width:40vw;position:relative}.fav-comp .list-container{height:calc(100% - 6em);overflow-y:auto;width:100%}

.button-comp{border:none;padding:0.8rem 1.2rem;border-radius:8px;font-family:'Roboto', sans-serif;font-size:1.1em;font-weight:bold;position:relative;cursor:pointer;color:white}.button-comp.disabled{cursor:not-allowed;color:lightgrey;padding:0.8rem 1.2rem;margin:0 !important}.button-comp.disabled:hover{box-shadow:none}.button-comp:hover{box-shadow:inset 0px 0px 0px 999px rgba(0,0,0,0.2)}.button-comp.color-default{background-color:#388E3C}.button-comp.color-default.is-inverse{color:#388E3C;background-color:transparent;box-shadow:inset 0px 0px 0px 2px #388E3C}.button-comp.color-default.is-inverse:hover{color:white;background-color:#388E3C}.button-comp.color-danger{background-color:#f44336}.button-comp.color-danger.is-inverse{color:#f44336;background-color:transparent;box-shadow:inset 0px 0px 0px 2px #f44336}.button-comp.color-danger.is-inverse:hover{color:white;background-color:#f44336}

.section-footer-comp{background-color:#A5D6A7;height:6em;width:100%;position:absolute;bottom:0;display:flex;justify-content:flex-end;align-items:center;padding:0 1rem}.section-footer-comp>*:not(:last-child){margin-right:0.8rem}

.cart-list-item-comp{min-width:22em;max-width:100%;min-height:100px;border-bottom:1px solid #388E3C;overflow-x:hidden}.cart-list-item-comp .header-container{text-transform:capitalize;font-weight:bold;font-size:1.2em;color:#388E3C;background-color:#C8E6C9;padding:0.6rem 1.5rem;display:flex;justify-content:space-between}.cart-list-item-comp .header-container .left-part{font-family:'Roboto', sans-serif;font-size:1.25em}.cart-list-item-comp .header-container .right-part>span{cursor:pointer}.cart-list-item-comp .header-container .right-part .delete-btn{margin-left:1rem;color:#f44336}.cart-list-item-comp .content{padding:0.5rem 1rem}.cart-list-item-comp .content span:not(:last-of-type)::after{content:", "}

.salad-maker-comp{height:100%;min-width:52em;width:65vw;position:relative;display:flex;justify-content:space-around}.salad-maker-comp .react-tabs{-webkit-tap-highlight-color:transparent}.salad-maker-comp .react-tabs__tab-list{border-bottom:1px solid #aaa;margin:0 0 10px;padding:0}.salad-maker-comp .react-tabs__tab{display:inline-block;border:1px solid transparent;border-bottom:none;bottom:-1px;position:relative;list-style:none;padding:6px 12px;cursor:pointer}.salad-maker-comp .react-tabs__tab--selected{background:#fff;border-color:#aaa;color:black;border-radius:5px 5px 0 0}.salad-maker-comp .react-tabs__tab--disabled{color:GrayText;cursor:default}.salad-maker-comp .react-tabs__tab:focus{box-shadow:0 0 5px #0188fe;border-color:#0188fe;outline:none}.salad-maker-comp .react-tabs__tab:focus:after{content:"";position:absolute;height:5px;left:-4px;right:-4px;bottom:-5px;background:#fff}.salad-maker-comp .react-tabs__tab-panel{display:none}.salad-maker-comp .react-tabs__tab-panel--selected{display:block}

.sm-ingredients-comp{width:50%}.sm-ingredients-comp .salad-tab-list{display:flex;list-style:none;margin:0;padding-left:1.5rem}.sm-ingredients-comp .salad-tab-list .salad-tab{padding:0.5rem 1rem;border-top-left-radius:8px;border-top-right-radius:8px;border-left:2px solid;border-top:2px solid;border-right:2px solid;cursor:pointer;text-transform:uppercase;font-family:'Roboto', sans-serif;font-weight:bold}.sm-ingredients-comp .salad-tab-list .salad-tab--selected{-webkit-transform:scale(1.15) translateY(-2px);transform:scale(1.15) translateY(-2px);color:white !important}.sm-ingredients-comp .salad-tab-list .salad-tab--selected.lettuces-tab{background-color:#4CAF50}.sm-ingredients-comp .salad-tab-list .salad-tab--selected.toppings-tab{background-color:#FF9800}.sm-ingredients-comp .salad-tab-list .salad-tab--selected.dressings-tab{background-color:#F57C00}.sm-ingredients-comp .salad-tab-panel{overflow:auto}.sm-ingredients-comp .salad-tab-panel--selected{margin-left:0.6rem;height:25em;border-top:2px solid}@-webkit-keyframes stroke{100%{stroke-dashoffset:0}}@keyframes stroke{100%{stroke-dashoffset:0}}@-webkit-keyframes scale{0%,100%{-webkit-transform:none;transform:none}50%{-webkit-transform:scale3d(1.1, 1.1, 1);transform:scale3d(1.1, 1.1, 1)}}@keyframes scale{0%,100%{-webkit-transform:none;transform:none}50%{-webkit-transform:scale3d(1.1, 1.1, 1);transform:scale3d(1.1, 1.1, 1)}}

.ingredient-list-item-comp{padding:1rem 1.3rem;display:flex;justify-content:space-between;border-bottom:1px solid grey;cursor:pointer;font-weight:bold;font-size:1.2em}.ingredient-list-item-comp .left-part{display:flex}.ingredient-list-item-comp:hover{background-color:#FAFAFA}.ingredient-list-item-comp .img-container{width:80px;height:60px;background-position:center;background-size:cover}.ingredient-list-item-comp .name{margin-left:1rem}

.checkmark-comp .checkmark{width:56px;height:56px;border-radius:50%;display:block;stroke-width:2;stroke:#4CAF50;stroke-miterlimit:10;box-shadow:inset 0px 0px 0px #4CAF50}.checkmark-comp .checkmark__check{-webkit-transform-origin:50% 50%;transform-origin:50% 50%;stroke-dasharray:48;stroke-dashoffset:48;-webkit-animation:stroke 0.5s cubic-bezier(0.65, 0, 0.45, 1) forwards;animation:stroke 0.5s cubic-bezier(0.65, 0, 0.45, 1) forwards}


.salad-overview-item-comp{width:22em;min-height:100px;border:2px solid;border-radius:8px;overflow-x:hidden}.salad-overview-item-comp:not(:last-child){margin-bottom:1rem}.salad-overview-item-comp.lettuces{border-color:#4CAF50}.salad-overview-item-comp.lettuces .header-container{background-color:#C8E6C9;color:#4CAF50}.salad-overview-item-comp.toppings{border-color:#FF9800}.salad-overview-item-comp.toppings .header-container{background-color:#FFE0B2;color:#FF9800}.salad-overview-item-comp.dressings{border-color:#F57C00}.salad-overview-item-comp.dressings .header-container{background-color:#FFCCBC;color:#F57C00}.salad-overview-item-comp .header-container{text-transform:capitalize;font-family:'Roboto', sans-serif;font-weight:bold;padding:0.6rem 1rem}.salad-overview-item-comp .content{padding:0.5rem 1rem}.salad-overview-item-comp .content span:not(:last-child)::after{content:", "}

.fav-list-comp{height:100%;width:100%}.fav-list-comp.flex{display:flex;justify-content:center;align-items:center}.fav-list-comp .no-salad-label{text-align:center;color:#388E3C;font-weight:bold;font-size:2em}.fav-list-comp .no-salad-label span{font-size:0.7em;font-weight:normal}

